@import '../base.scss';

.c-points-and-bundle{
  @include t-v-gutters;
  @include t-gutters;

  &__title{
    text-align: center;
    color: $s-color--font-1;
    margin-bottom: t-to-rem(40);
    &.is-desktop{
      display: none;
    }
  }

  &__products{
    display: flex;
    position: relative;
  }


  &__product{
    width: 50%;
    position: relative;
    &.is-product-odd{
      background-color: $s-color--background-quaternary;
    }
    &.is-product-even{
      background-color: $s-color--background-tertiary;
    }

    &-image{
      height: 70%;
      width: initial;
      @include t-absolute-center-x-y;
      max-width: 70%;
      object-fit: contain;
    }
  }

  &__image{
    width: 100%;
    padding-bottom: 70%;
    position: relative;
  }

  &__generic-image{
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &__details{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: t-to-rem(26) 0 t-to-rem(38); 
    text-align: center;

    &-title{
      margin-bottom: t-to-rem(26);
    }
  }

  &__swatches{
    margin-bottom: t-to-rem(14);
    width: 100%;
  }

  &__add-cart{
    margin: 0 auto;

    &.o-btn{
      display: flex;

    }

    .is-divider{
      margin: 0 t-to-rem(4);
    }

    &-price{
      font-size: inherit;
      display: inline;
      color: $s-color--font-6;
      &-compare{
        text-decoration: line-through;
        color: rgba($s-color--font-6, .5);
        margin-right: t-to-rem(8);
      }
    }
  }
  

  &__point{
    display: flex;
    align-items: center;
    margin-bottom: t-to-rem(13);
    &:last-child{
      margin-bottom: 0;
    }
    &-icon{
      @include t-width-and-height(41);
      position: relative;
      background-color: $s-color--background-quaternary;
      border-radius: 100%;
      margin-right: t-to-rem(11);
      flex-shrink: 0;
      svg{
        @include t-absolute-center-x-y;
        @include t-width-and-height(20);

      }
    }
    &-text{
      color: $s-color--blue-green;
    }
  }


  @include t-media-query($s-medium-down) {

    &__items{
      &.is-desktop{
        display: none;
      }
      text-align: center;
      margin-bottom: t-to-rem(24);
    }

    &__bundle-container{
      margin-bottom: t-to-rem(42);
      
    }
    &__content-container{
      max-width: t-to-rem(400);
      margin: 0 auto;
    }
    &__products{
      height: t-to-rem(258);
      &.has-image{
        margin: auto;
        max-width: 25rem;
      }
      &.is-desktop-only{
        display: none;
      }
    }

  }
  @include t-media-query($s-small-up) {
    
    &__bundle-container{
      order: 2;
      width: 62.67%;
      flex-shrink: 0;
      margin-left: auto;
      margin-right: auto;
      position: relative;
  
    }

  }


  @include t-media-query($s-medium-up) {
    display: flex;
    
    &__items{
      &.is-mobile{
        display: none;
      }
      margin-bottom: 12%;
    }

    &__title{
      &.is-mobile{display: none;}
      &.is-desktop{display: block;}
      text-align: left;
      margin-bottom: 11%;
    }

    &__bundle-container{
      display: flex;
    }

    &__swatches{
      padding: t-to-rem(20) t-to-rem(30);
      background: $s-color--background;
    }
    

    &__content-container{
      margin-right: 7%;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    &__products{
      flex: 1;
    }

    &__details{
      @include t-absolute-center-x-y;
      &-title{
        display: none;
      }
    }

    &__point{
      margin-bottom: 8.5%;
      &-icon{
        margin-right: t-to-rem(22);
        @include t-width-and-height(56);
        svg{
          @include t-width-and-height(28);  
        }
      }
    }
    &__add-cart{
      &.o-btn{
        padding: t-to-rem(15) t-to-rem(30);
      }
      
      .is-divider{
        margin: 0 t-to-rem(6);
      }
    }

  }
  @include t-media-query($s-xlarge-up) {
    &__title{
      width: 92%;
    }
  }
}